.ports-page.ports-vertical-slides {
  width: 100%;
  height: 100%;
  background-color: #3D3D3D;
  color: #F9F8F6;
}

.ports-page.ports-vertical-slides .slide {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-size: cover;
}

.ports-page.ports-vertical-slides .slide .cta {
  position: absolute;
  bottom: 5%;
  font-weight: bold;
  font-size: 10px;
  color: #F9F8F6;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s;
  transition-delay: 0;
  left: 50%;
  transform: translateX(-50%);
  border-bottom: 1px solid #F9F8F6;
  padding-bottom: 4px;
  text-align: center;
}

.ports-page.ports-vertical-slides .slide.should-show-cta .cta,
.ports-page.ports-vertical-slides .slide.in-view .cta {
  pointer-events: auto;
  opacity: 1;
  transition-delay: 0.25s;
}

.ports-home-slide-layout-full a {
  width: 100%;
  height: 100%;
}

.ports-home-slide-layout-full .asset-full .css-image,
.ports-home-slide-layout-full .asset-right .css-image,
.ports-home-slide-layout-full .asset-left .css-image {
  background-size: cover;
}

.ports-home-slide-layout-group-4 a {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.ports-home-slide-layout-group-4 a .asset-1,
.ports-home-slide-layout-group-4 a .asset-2,
.ports-home-slide-layout-group-4 a .asset-3,
.ports-home-slide-layout-group-4 a .asset-4 {
  width: 20%;
  margin: 15px;
}

.ports-home-slide-layout-left a {
  width: 100%;
  height: 100%;
}

.ports-home-slide-layout-left a .asset-left {
  width: 50%;
}

.ports-home-slide-layout-left a .asset-left .css-image {
  background-size: cover;
}

.ports-home-slide-layout-left a .cta {
  left: 25% !important;
}

.ports-home-slide-layout-left-right a {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

.ports-home-slide-layout-left-right a .asset-left,
.ports-home-slide-layout-left-right a .asset-right {
  width: 50%;
  height: 100%;
}

.ports-home-slide-layout-left-right a .asset-left .css-image,
.ports-home-slide-layout-left-right a .asset-right .css-image {
  background-size: cover;
}

.ports-home-slide-layout-right a {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
}

.ports-home-slide-layout-right a .asset-right {
  width: 50%;
}

.ports-home-slide-layout-right a .asset-right .css-image {
  background-size: cover;
}

.ports-home-slide-layout-right a .cta {
  left: 75% !important;
}

.ports-home-slide-iframe-layout {
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.ports-home-slide-iframe-layout a {
  height: 100%;
  width: 100%;
}

.ports-home-slide-iframe-layout iframe {
  background-color: black;
  height: 100%;
  width: 100%;
}

.ports-home-slide-vimeo-video {
  background: black;
}

.ports-home-slide-vimeo-video .preview-video {
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.ports-home-slide-vimeo-video .full-video {
  width: 100%;
  height: 100%;
}

.ports-page .ports-home-slide-layout-left-right .ports-home-slide-layout-left,
.ports-page .ports-home-slide-layout-left-right .ports-home-slide-layout-right {
  height: 100%;
  width: 50%;
}

.ports-page .ports-home-slide-layout-left-right .ports-home-slide-layout-left .cta,
.ports-page .ports-home-slide-layout-left-right .ports-home-slide-layout-right .cta {
  display: none;
}

.ports-page .ports-home-slide-layout-left-right .asset-left,
.ports-page .ports-home-slide-layout-left-right .asset-right {
  width: 100%;
}

.ports-page .ports-home-slide-iframe-layout a {
  display: block;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  position: relative;
}

.ports-page .ports-home-slide-iframe-layout a iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 100vh;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

@media (max-width: 768px) {
  .ports-home-slide-layout-group-4 a {
    flex-wrap: wrap;
    align-items: center;
  }

  .ports-home-slide-layout-group-4 a .asset-1,
  .ports-home-slide-layout-group-4 a .asset-2,
  .ports-home-slide-layout-group-4 a .asset-3,
  .ports-home-slide-layout-group-4 a .asset-4 {
    width: 45%;
    margin: 5px;
    height: 35%;
  }

  .ports-home-slide-layout-group-4 a .asset-1 .css-image,
  .ports-home-slide-layout-group-4 a .asset-2 .css-image,
  .ports-home-slide-layout-group-4 a .asset-3 .css-image,
  .ports-home-slide-layout-group-4 a .asset-4 .css-image {
    background-size: cover;
    background-position: top;
  }

  .ports-home-slide-layout-group-4 a .asset-1,
  .ports-home-slide-layout-group-4 a .asset-2 {
    transform: translateY(20%);
  }

  .ports-home-slide-layout-group-4 a .asset-3,
  .ports-home-slide-layout-group-4 a .asset-4 {
    transform: translateY(-20%);
  }
}

@media screen and (max-width: 768px) {
  .ports-page .ports-home-slide-layout-left-right a {
    flex-direction: column;
  }

  .ports-page .ports-home-slide-layout-left-right a > .cta {
    display: none;
  }
}

@media (min-aspect-ratio: 16 / 9) {
  .ports-page .ports-home-slide-iframe-layout a iframe {
    /* height = 100 * (9 / 16) = 56.25 */
    height: 56.25vw;
  }
}

@media (max-aspect-ratio: 16 / 9) {
  .ports-page .ports-home-slide-iframe-layout a iframe {
    /* width = 100 / (9 / 16) = 177.777777 */
    width: 177.78vh;
  }
}